.App {
  margin: 5em;
}

.App-interface {
  margin-top: 2em;
  margin-left: 5em;
}

.App-interface #height {
  width: 1.5em;
  text-align: right;
}

.App-interface #width {
  width: 1.5em;
  text-align: right;
}

.App-interface .spaced-element {
  margin-left: 1.5em;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}
