body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin: 5em;
}

.App-interface {
  margin-top: 2em;
  margin-left: 5em;
}

.App-interface #height {
  width: 1.5em;
  text-align: right;
}

.App-interface #width {
  width: 1.5em;
  text-align: right;
}

.App-interface .spaced-element {
  margin-left: 1.5em;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}

.tile {
  height: 30px;
  width: 30px;
  background: none;
  background-size: 30px 30px;
  border-style: solid;
  opacity: 1;
}
.tile:focus {
  outline: none;
}
.tile.grass {
  background: darkseagreen;
  border-color: green;
  border-style: dotted;
}
.tile.water {
  background: aqua;
  border-color: peru;
  border-style: dotted;
}
.tile.rock {
  background-image: url(data:image/png;base64,);
}
.tile.tree {
  background-image: url(data:image/png;base64,);
  border-style: dotted;
}
